import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';
@Component({
  selector: 'app-promedio',
  templateUrl: './promedio.component.html',
  styleUrls: ['./promedio.component.css']
})
export class PromedioComponent implements OnInit {
  courses: any[] = [];
  promedio: any;
  constructor(private api: ApiService) {

    this.api.getItems('cursos').subscribe((courses) => {
      this.courses = courses;
      console.log('los cursos', courses);
    });
   }

  ngOnInit() {
  }

  changeCourse(course_id) {
    this.api.getPromedioCurso(course_id).subscribe(data => {
      console.log('promedio dentro del componente', data.promedio);
      this.promedio = data.promedio;
    });
  }

}
